import React, { Component, PropTypes } from 'react';

import { immutableRenderDecorator } from 'react-immutable-render-mixin';
import { Seq } from 'immutable';
import CSSModules from 'react-css-modules';
import classnames from 'classnames';

import TabNav from 'components/TabNav';
import TabContent from 'components/TabContent';
import Emitter from 'components/events';

import styles from 'styles/App.scss';

@immutableRenderDecorator
@CSSModules(styles, { allowMultiple: true })
class Tabs extends Component {
    //Props
    static propTypes = {
        children: PropTypes.oneOfType([
            PropTypes.arrayOf(PropTypes.node),
            PropTypes.node,
        ]),

        defaultActiveIndex: PropTypes.number,
        activeIndex: PropTypes.number,
        onChange: PropTypes.func,
    };

    constructor(props) {
        super(props);

        const currProps = this.props;
        let activeIndex;
        activeIndex = currProps.defaultActiveIndex;

        //ImmutableSeqchildren
        //seq
        this.immChildren = Seq(currProps.children);
        this.handleTabClick = this.handleTabClick.bind(this);

        //activeIndex state
        this.state = {
            activeIndex,  //activeIndex
            prevIndex: activeIndex  //activeIndex
        };
    }

    handleTabClick(activeIndex) {
        const prevIndex = this.state.activeIndex;

        //activeIndexactiveIndex
        //propsdefaultActiveIndex
        if (this.state.activeIndex !== activeIndex &&
            'defaultActiveIndex' in this.props) {
            this.setState({
                activeIndex,
                prevIndex
            });
        }
    }

    /**
     * Event
     */
    componentDidMount() {
        this.itemChange = Emitter.on('itemChange', (activeIndex) => {
            this.handleTabClick(activeIndex);
        });
    }

    componentWillUnmount() {
        Emitter.removeListener(this.itemChange);
    }

    /**
     * Tabs
     */
    renderTabNav() {
        return (
            <TabNav
                key='tabBar'
                onTabClick={this.handleTabClick}  //
                panels={this.immChildren}
                activeIndex={this.state.activeIndex}
                />
        );
    }

    /**
     * Tabs
     */
    renderTabContent() {
        return (
            <TabContent
                key='tabContent'
                activeIndex={this.state.activeIndex}
                panels={this.immChildren}
                />
        );
    }

    render() {
        const { className} = this.props;
        const cx = classnames(className,'ui-tabs');

        return (
            <div className={cx}>
            {this.renderTabNav()}
            {this.renderTabContent()}
            </div>
        );
    }
}

export default Tabs;


