//
require('normalize.css/normalize.css');

//
require('styles/App.scss');

import React, { Component } from 'react';
import Tabs from 'components/Tabs';
import Tabpane from 'components/TabPane';
import emitter from 'components/events';

class App extends Component {
    constructor(props) {
        super(props);

        this.handleChange = this.handleChange.bind(this);

        this.state = {
            activeIndex: 0,
        };
    }

    handleChange(e) {
        let index = parseInt(e.target.value, 10);
        emitter.emit('itemChange', index);

        this.setState({
            activeIndex: index,
        });
    }

    render() {
        return (
            /**/
            <div>
                <div className="operator">
                    <span> Tab: </span>
                    <select value={this.state.activeIndex}
                        onChange={this.handleChange}>
                        <option value="0">Tab 1</option>
                        <option value="1">Tab 2</option>
                        <option value="2">Tab 3</option>
                    </select>
                </div>
                {/**/}
                <Tabs defaultActiveIndex={this.state.activeIndex}
                    className="tabs-bar">
                    {/* 
                     Tabpane
                     propsTabNav */}
                    <Tabpane order="0" tab={'Tab 1'}> Tab </Tabpane>
                    <Tabpane order="1" tab={'Tab 2'}> Tab </Tabpane>
                    <Tabpane order="2" tab={'Tab 3'}> Tab </Tabpane>
                </Tabs>
            </div >
        );
    }
}

export default App;


